import { useRef, useMemo } from 'react'
import css from './SliderPartners.module.scss'
import classnames from 'classnames'
import SliderProvider from 'components/Molecules/SliderProvider/SliderProvider'
import {SwiperSlide} from 'swiper/react'
import IconSliderArrow from 'assets/icons/IconSliderArrow'

function SliderPartners ({
  className,
  data,
  isWithButtons = false,
  SlideComponent,
  ...props
}) {
  const isButtonsRenderRequired = data.length > 1 && isWithButtons
  const buttonPrevRef = useRef(null)
  const buttonNextRef = useRef(null)

  const slides = useMemo(() => {
    return data
      .map((slide, index) => (
        <SwiperSlide
          tag={ `li` }
          key={index}
        >
          <SlideComponent {...slide} />
        </SwiperSlide>
      ))
  }, [data])

  const controls = useMemo(() => {
    if (isButtonsRenderRequired) {
      return (
        <>
          <button
            className={css.button}
            type="button"
            ref={buttonPrevRef}
          >
            Попередній слайд
            <IconSliderArrow className={css.icon} />
          </button>
          <button
            className={classnames(css.button, css.buttonNext)}
            type="button"
            ref={buttonNextRef}
          >
            Наступний слайд
            <IconSliderArrow className={css.icon} />
          </button>
        </>
      )
    }
  }, [isButtonsRenderRequired])

  return (
    <SliderProvider
      className={css.wrapper}
      slides={slides}
      {...props}
    >
      { controls }
    </SliderProvider>
  )
}

export default SliderPartners
