import React, { useEffect, useRef } from 'react'
import css from './InputFile.module.scss'
import classnames from 'classnames'
import { removeDuplicateFiles } from 'utils'
import IconClose from 'assets/icons/IconClose'

function InputFile ({
  className,
  formats,
  onChange,
  onDeleteCallback,
  value = [],
  defaultValue,
  name,
  formRef,
  setValue,
  label = `Додати фото`,
  isError,
  isMultiple,
  children,
  callbackOnChange,
  ...props
}) {
  const isMounted = useRef(false)
  const acceptedFormats = formats.join(`, `)

  useEffect(() => {
    if (Array.isArray(defaultValue) && defaultValue.length > 0 && value !== defaultValue && !isMounted.current) {
      onChange(defaultValue)
    }
    isMounted.current = true
  }, [defaultValue, onChange, value])
  
  useEffect(() => {
    if (callbackOnChange) {
      callbackOnChange(value)
    }
  }, [value])
  
  function renderDeleteButton () {
    const isNotEmptyAndSingleSelection = Array.isArray(value) && value.length > 0 && !isMultiple

    return isNotEmptyAndSingleSelection
      ? (
        <button
          onClick={deleteFile}
          className={css.button}
          type={ `button` }
        >
          <IconClose className={css.icon} />
          Видалити файл
        </button>
      )
      : null
  }

  function deleteFile () {
    setValue(name, ``)
    onDeleteCallback && onDeleteCallback()
  }

  return (
    <div
      className={classnames(className, {
        [css.wrapperError]: isError,
      })}
    >
      <label
        className={css.label}
        htmlFor={name}
      >
        { children }
      </label>
      <input
        name={name}
        className={css.input}
        type='file'
        id={name}
        accept={acceptedFormats}
        files={value}
        onChange={evt => {
          const addedFiles = isMultiple ? removeDuplicateFiles(evt.target.files, value) : evt.target.files
          onChange && onChange(isMultiple ? [...value, ...addedFiles] : [...addedFiles])
        }}
        ref={formRef}
        multiple={isMultiple}
        {...props}
      />
    </div>
  )
}

export default InputFile
