import { useSelector } from 'react-redux'
import { partnersData } from 'utils/dataAbout'
import SideInfoBlock from 'components/Molecules/SideInfoBlock/SideInfoBlock'
import AboutCompanyPartnersTemplate
  from 'components/Templates/AboutCompany/AboutCompanyPartnersTemplate/AboutCompanyPartnersTemplate'
import SlideLogo from 'components/Atoms/slides/SlideLogo/SlideLogo'
import SliderPartners from 'components/Molecules/SliderPartners/SliderPartners'

function AboutCompanyPartners () {
  const lang = useSelector(store => store.ui.lang)

  const {
    info,
    list
  } = partnersData[lang]

  return (
    <section>
      <AboutCompanyPartnersTemplate>
        <SideInfoBlock {...info} />
        <SliderPartners
          data={list}
          SlideComponent={SlideLogo}
          freeMode={true}
          slidesPerView={3.5}
          autoplay={{
            delay: 1,
            pauseOnMouseEnter: true,
            disableOnInteraction: false
          }}
          speed={4000}
          loop={true}
          allowTouchMove={false}
        />
      </AboutCompanyPartnersTemplate>
    </section>
  )
}

export default AboutCompanyPartners
